<template>
  <div id="app">
    <div id="cover"></div>
    <Header></Header>
    <Todo></Todo>
    <Footer></Footer>
  </div>
</template>

<script>
import Header from '../layout/header.vue'
import Footer from '../layout/footer.jsx'
import Todo from './todo/todo.vue'


export default {
  components: {
    Header,
    Footer,
    Todo,
  }
}
</script>

<!-- scoped指定的样式只在此组件内有效 -->
<style lang="stylus" scoped> 
#app {
  position absolute
  left 0
  right 0
  top 0
  bottom 0
}
#cover{
  position absolute
  left 0
  top 0
  right 0
  bottom 0
  background-color #999
  opacity .9
  z-index -1
}
</style>



